.amountBox {
    padding: 15px 5px 15px 10px;
    display: flex;
    justify-content: space-between;
    color: #ffffff;
    font-size: 18px;
    background: transparent;
    border: 2px solid var(--color-secondary);
    border-radius: 4px;
}

.amountBox>p {
    margin-right: 25px;
}

.amountBox>span {
    text-align: center;
    width: 20px;
}

.button {
    width: 24px;
    height: 24px;
    margin: 0 5px;
    border: none;
    background: transparent;
    cursor: pointer;
}

.button:focus {
    transform: scale(1.2);
}

.flex {
    display: flex;
    align-items: center;
}